.navigation-top {

}

.navigation-top__wrap {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: space-between;
  align-items: center;
  padding-bottom: 17px;

  @media (max-width: 1600px) {

  }

  @media (max-width: 992px) {
    grid-template-columns: 1fr;
    justify-content: center;
  }
}

.navigation-top__logo {
  margin-top: 30px;

  @media (max-width: 992px) {
    margin-top: 5.54vw;
  }
}
