.wrapper {
  max-width: 1920px;
  padding-left: 8.33vw;
  padding-right: 8.33vw;
  margin: 0 auto;

  @media (max-width: 1600px) {
    padding-left: 8.13vw;
    padding-right: 8.13vw;
  }

  @media (max-width: 1280px) {
    padding-left: 8.4vw;
    padding-right: 8.4vw;
  }

  @media (max-width: 1080px) {
    padding-left: 6vw;
    padding-right: 6vw;
  }
}
