.delivery {
  display: grid;
  grid-template-columns: repeat(2, max-content);
  justify-content: space-between;
  width: 780px;
  background-color: #eae6e3;
  padding-top: 35px;
  padding-left: 40px;
  padding-right: 40px;
  padding-bottom: 35px;

  @media (max-width: 1600px) {
    width: 667px;
    padding-top: 30px;
    padding-left: 32px;
    padding-right: 32px;
    padding-bottom: 30px;
  }

  @media (max-width: 1280px) {
    width: 533px;
    padding-top: 25px;
    padding-left: 25px;
    padding-right: 25px;
    padding-bottom: 25px;
  }

  @media (max-width: 1080px) {
    width: 780px;
    padding-top: 35px;
    padding-left: 40px;
    padding-right: 40px;
    padding-bottom: 35px;
  }

  @media (max-width: 992px) {
    grid-template-columns: 1fr;
    width: 100%;
    padding-top: 27px;
    padding-left: 70px;
    padding-right: 65px;
    padding-bottom: 35px;
  }
}

.delivery p,
.delivery li {
  font-family: inherit;
  font-size: 34px;
  line-height: 35px;
  font-weight: normal;

  @media (max-width: 1600px) {
    font-size: 28px;
    line-height: 30px;
  }

  @media (max-width: 1280px) {
    font-size: 23px;
    line-height: 25px;
  }

  @media (max-width: 1080px) {
    font-size: 34px;
    line-height: 35px;
  }
}

.delivery p {
  margin-bottom: 25px;

  @media (max-width: 1600px) {
    margin-bottom: 23px;
  }

  @media (max-width: 1280px) {
    margin-bottom: 20px;
  }

  @media (max-width: 1080px) {
    margin-bottom: 26px;
  }

  @media (max-width: 992px) {
    margin-bottom: 23px;
  }
}

.delivery li {
  position: relative;
  font-weight: 100;
  padding-left: 23px;
}

.delivery li::before {
  position: absolute;
  content: "";
  width: 8px;
  height: 8px;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  background-color: var(--back-main);
}

.delivery__item {
  @media (max-width: 992px) {
    margin-bottom: 38px;
  }
}

.delivery__item:last-child {
  @media (max-width: 992px) {
    margin-bottom: 0;
  }
}
