.navigation-slider {
  min-height: 80px;
  background-color: var(--basic-green);
  padding-top: 15px;
  padding-bottom: 15px;

  @media (max-width: 1600px) {
    min-height: 67px;
    padding-top: 13px;
    padding-bottom: 13px;
  }

  @media (max-width: 1280px) {
    min-height: 53px;
    padding-top: 10px;
    padding-bottom: 10px;
  }

  @media (max-width: 1080px) {

  }
}

.navigation-slider__wrap.swiper-container {
  padding-left: 160px;
  padding-right: 160px;

  @media (max-width: 1600px) {
    padding-left: 134px;
    padding-right: 134px;
  }

  @media (max-width: 1280px) {
    padding-left: 107px;
    padding-right: 107px;
  }

  @media (max-width: 1080px) {
    padding-left: 158px;
    padding-right: 158px;
  }

  @media (max-width: 992px) {
    padding-left: 50px;
    padding-right: 50px;
  }
}

.navigation-slider__list.swiper-wrapper {


  justify-items: center;

  background: orange;

  @media (max-width: 1080px) {

  }
}

.navigation-slider__item.swiper-slide {
  text-align: center;

  background: green;

  outline: 2px solid red;

}

.navigation-slider__item a {
  font-family: "Lady Marmalade", "Times", serif;
  font-size: 34px;
  line-height: 48px;
  font-weight: 300;
  text-transform: uppercase;
  color: var(--basic-white);
  text-decoration: none;
  padding-top: 12px;
  padding-left: 12px;
  padding-right: 12px;
  padding-bottom: 7px;

  @media (max-width: 1600px) {
    font-size: 28px;
    line-height: 40px;
    padding-top: 9px;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 7px;
  }

  @media (max-width: 1280px) {
    font-size: 23px;
    line-height: 32px;
    padding-top: 8px;
    padding-bottom: 5px;
  }

  @media (max-width: 1080px) {
    font-size: 34px;
    line-height: 48px;
    padding-top: 12px;
    padding-left: 12px;
    padding-right: 12px;
    padding-bottom: 7px;
  }
}

.navigation-slider__item--active {

}

a.navigation-slider__link-active {
  color: var(--basic-blue);
  background-color: var(--basic-white);
}

.navigation-slider__toggle {
  position: absolute;
  display: block;
  width: 18px;
  height: 27px;
  top: 12px;
  background: url("../img/arrow-white.png");
  background-repeat: no-repeat;
  background-size: 100% auto;
  cursor: pointer;
  z-index: 15;

  @media (max-width: 1600px) {
    width: 16px;
    height: 23px;
    top: 10px;
  }

  @media (max-width: 1280px) {
    width: 13px;
    height: 19px;
    top: 8px;
  }

  @media (max-width: 1080px) {
    width: 18px;
    height: 27px;
    top: 12px;
  }
}

.navigation-slider__toggle--prev {
  left: 0;
  transform: scaleX(-1);
}

.navigation-slider__toggle--next {
  right: 0;
}

.navigation-slider .container {
  @media (max-width: 1080px) {
    padding-left: 20px;
    padding-right: 20px;
  }
}
