.goods-card {

}

.goods-card h1 {
  font-family: "Lady Marmalade", "Times", serif;
  font-size: 50px;
  line-height: 50px;
  text-transform: uppercase;
  color: var(--back-red);
  margin: 0;

  @media (max-width: 1600px) {
    font-size: 42px;
    line-height: 43px;
  }

  @media (max-width: 1280px) {
    font-size: 34px;
    line-height: 35px;
  }

  @media (max-width: 1080px) {
    font-size: 50px;
    line-height: 50px;
  }

  @media (max-width: 992px) {
    font-size: 42px;
    line-height: 43px;
  }
}

.goods-card__wrap {
  display: grid;
  grid-template-columns: max-content 780px;
  column-gap: 120px;
  margin-top: 53px;
  margin-bottom: 36px;

  @media (max-width: 1600px) {
    grid-template-columns: max-content 667px;
    column-gap: 100px;
    margin-top: 38px;
    margin-bottom: 35px;
  }

  @media (max-width: 1280px) {
    grid-template-columns: max-content 533px;
    column-gap: 80px;
    margin-top: 30px;
    margin-bottom: 24px;
  }

  @media (max-width: 1080px) {
    grid-template-columns: 1fr;
    margin-top: 35px;
    margin-bottom: 34px;
  }

  @media (max-width: 992px) {
    margin-top: 30px;
  }
}

.goods-card__slider {
  @media (max-width: 992px) {
    margin-bottom: 26px;

    outline: 2px solid blue;
  }
}

.goods-card__desc {

}

.goods-card__desc p,
.goods-card__desc span {
  font-family: inherit;
  font-size: 34px;
  line-height: 36px;
  font-weight: 100;
  margin: 0;

  @media (max-width: 1600px) {
    font-size: 28px;
    line-height: 30px;
  }

  @media (max-width: 1280px) {
    font-size: 23px;
    line-height: 25px;
  }

  @media (max-width: 1080px) {
    font-size: 34px;
    line-height: 36px;
  }
}

.goods-card__desc span {
  display: block;
  font-size: 25px;
  color: var(--basic-normal-blue);
  margin-top: 40px;

  @media (max-width: 1600px) {
    font-size: 21px;
  }

  @media (max-width: 1280px) {
    font-size: 17px;
  }

  @media (max-width: 1080px) {
    font-size: 25px;
  }
}
