.sidebar-list {
  margin-top: 32px;
  margin-bottom: 108px;

  @media (max-width: 1600px) {
    margin-top: 28px;
    margin-bottom: 90px;
  }

  @media (max-width: 1280px) {
    margin-top: 21px;
    margin-bottom: 70px;
  }

  @media (max-width: 1080px) {
    margin-top: 32px;
    margin-bottom: 108px;
  }
}

.sidebar-list li {
  font-family: inherit;
  font-size: 25px;
  text-transform: uppercase;
  margin-bottom: 16px;

  @media (max-width: 1600px) {
    font-size: 21px;
    margin-bottom: 14px;
  }

  @media (max-width: 1280px) {
    font-size: 17px;
    margin-bottom: 12px;
  }

  @media (max-width: 1080px) {
    font-size: 25px;
    margin-bottom: 16px;
  }
}

.sidebar-list a {
  color: var(--back-main);
  text-decoration: none;
}

a.sidebar-list__link-active {
  color: var(--back-red);
}
