.form-users {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 87px;
  align-items: start;

  @media (max-width: 1600px) {
    column-gap: 76px;
  }

  @media (max-width: 1280px) {
    column-gap: 60px;
  }

  @media (max-width: 1080px) {
    column-gap: 87px;
  }
}

.form-users fieldset {
  border: none;
  padding: 0;
  margin: 0;
}

.form-users legend {
  font-family: inherit;
  font-size: 25px;
  text-transform: uppercase;
  color: var(--back-red);
  padding: 0;
  margin-bottom: 23px;

  @media (max-width: 1600px) {
    font-size: 21px;
    margin-bottom: 18px;
  }

  @media (max-width: 1280px) {
    font-size: 17px;
    margin-bottom: 15px;
  }

  @media (max-width: 1080px) {
    font-size: 25px;
    margin-bottom: 21px;
  }
}

.form-users li {
  padding-left: 28px;

  @media (max-width: 1600px) {
    padding-left: 24px;
  }

  @media (max-width: 1280px) {
    padding-left: 20px;
  }

  @media (max-width: 1080px) {
    padding-left: 30px;
  }
}

.form-users label {
  position: relative;
  font-family: inherit;
  font-size: 25px;
  text-transform: uppercase;

  @media (max-width: 1600px) {
    font-size: 21px;
  }

  @media (max-width: 1280px) {
    font-size: 17px;
  }

  @media (max-width: 1080px) {
    font-size: 25px;
  }
}

.form-users input[type="radio"] + label::before,
.form-users input[type="radio"] + label::after {
  position: absolute;
  content: "";
  width: 15px;
  height: 15px;
  top: 50%;
  transform: translateY(-50%);
  left: -28px;
  border: 2px solid var(--basic-normal-blue);
  border-radius: 50%;
  background-color: transparent;
  cursor: pointer;

  @media (max-width: 1600px) {
    width: 13px;
    height: 13px;
    left: -24px;
  }

  @media (max-width: 1280px) {
    width: 10px;
    height: 10px;
    left: -20px;
  }

  @media (max-width: 1080px) {
    width: 15px;
    height: 15px;
    left: -30px;
  }
}

.form-users input[type="radio"]:checked + label::after {
  background-color: var(--basic-normal-blue);
}
