.slider-card-thumbs {
  margin-top: 20px;

  @media (max-width: 1600px) {
    margin-top: 15px;
  }

  @media (max-width: 1280px) {
    margin-top: 12px;
  }

  @media (max-width: 1080px) {
    margin-top: 18px;
  }

  @media (max-width: 992px) {
    margin-top: 13px;
  }
}

.slider-card-thumbs__list {
  justify-content: center;
}

.slider-card-thumbs__item.swiper-slide {
  display: flex;
  width: 135px;
  height: 135px;
  border: 2px solid transparent;
  cursor: pointer;

  outline: 1px solid blue;

  @media (max-width: 1600px) {
    width: 112px;
    height: 112px;
  }

  @media (max-width: 1280px) {
    width: 90px;
    height: 90px;
  }

  @media (max-width: 1080px) {
    width: 135px;
    height: 135px;
  }

  @media (max-width: 992px) {
    width: 95px;
    height: 95px;
  }
}

.slider-card-thumbs figure {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  margin: 0 auto;
  align-items: center;
}

.slider-card-thumbs img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: contain;
}

.slider-card-thumbs.swiper-slide-thumb-active {
  border: 2px solid var(--back-red);
}

.slider-card-thumbs.swiper-container-autoheight figure {
  width: 100%; /* Ширина области */
  height: 178px; /* Высота области */
  margin: 0; /* Обнуляем отступы */
  overflow: hidden; /* Прячем всё за пределами */
}
