.user-order__form {
  margin-top: 28px;

  @media (max-width: 1600px) {
    margin-top: 25px;
  }

  @media (max-width: 1280px) {
    margin-top: 20px;
  }

  @media (max-width: 1080px) {
    margin-top: 32px;
  }

  @media (max-width: 992px) {
    margin-top: 30px;
  }
}

.user-order__buy {
  display: flex;
  align-items: center;
  font-family: inherit;
  margin-top: 40px;

  @media (max-width: 1600px) {
    margin-top: 34px;
  }

  @media (max-width: 1280px) {
    margin-top: 26px;
  }

  @media (max-width: 1080px) {
    margin-top: 40px;
  }

  @media (max-width: 992px) {
    margin-top: 34px;
  }
}

.user-order__buy p {
  font-size: 58px;
  font-weight: normal;
  color: var(--back-red);

  @media (max-width: 1600px) {
    font-size: 48px;
  }

  @media (max-width: 1280px) {
    font-size: 38px;
  }

  @media (max-width: 1080px) {
    font-size: 58px;
  }
}

.user-order__btn {
  width: 247px;
  margin-left: 83px;

  @media (max-width: 1600px) {
    width: 204px;
    margin-left: 69px;
  }

  @media (max-width: 1280px) {
    width: 165px;
    margin-left: 55px;
  }

  @media (max-width: 1080px) {
    width: 247px;
    margin-left: 83px;
  }

  @media (max-width: 992px) {
    width: 247px;
    margin-top: 20px;
    margin-left: 0;
  }
}

.user-order__delivery {
  margin-top: 60px;

  @media (max-width: 1600px) {
    margin-top: 50px;
  }

  @media (max-width: 1280px) {
    margin-top: 40px;
  }

  @media (max-width: 1080px) {
    margin-top: 60px;
  }

  @media (max-width: 992px) {
    margin-top: 28px;
  }
}
