.collection-goods {
  display: grid;
  grid-template-columns: max-content 1fr;
  column-gap: 80px;
  justify-items: center;
  margin-bottom: 55px;

  @media (max-width: 1600px) {
    column-gap: 67px;
    margin-bottom: 46px;
  }

  @media (max-width: 1280px) {
    column-gap: 54px;
    margin-bottom: 36px;
  }

  @media (max-width: 1080px) {
    column-gap: 68px;
    margin-bottom: 46px;
  }

  @media (max-width: 992px) {
    grid-template-columns: 1fr;
  }
}

