.gallery-inner {
  @media (max-width: 1080px) {
    text-align: center;
  }
}

.gallery-inner h2 {
  font-family: "Lady Marmalade", "Times", serif;
  font-size: 50px;
  line-height: 50px;
  text-transform: uppercase;
  color: var(--back-red);
  margin: 0;
  margin-bottom: 34px;

  @media (max-width: 1600px) {
    font-size: 42px;
    line-height: 42px;
    margin-bottom: 30px;
  }

  @media (max-width: 1280px) {
    font-size: 34px;
    line-height: 34px;
    margin-bottom: 23px;
  }

  @media (max-width: 1080px) {
    font-size: 50px;
    line-height: 50px;
    margin-bottom: 34px;
  }
}

.gallery-inner--product h2 {
  text-align: center;
  color: var(--basic-blue);
  margin-bottom: 0;
}

.gallery-inner__text {
  font-family: inherit;
  font-size: 34px;
  line-height: 36px;
  font-weight: 100;
  color: var(--back-main);
  margin: 0;

  @media (max-width: 1600px) {
    font-size: 28px;
    line-height: 30px;
  }

  @media (max-width: 1280px) {
    font-size: 23px;
    line-height: 25px;
  }

  @media (max-width: 1080px) {
    font-size: 34px;
    line-height: 36px;
  }
}

.gallery-inner__cards {
  display: grid;
  grid-template-columns: repeat(3, max-content);
  column-gap: 59px;
  align-items: start;
  row-gap: 80px;
  margin-top: 70px;

  @media (max-width: 1600px) {
    row-gap: 65px;
    margin-top: 58px;
  }

  @media (max-width: 1280px) {
    row-gap: 52px;
    margin-top: 47px;
  }

  @media (max-width: 1080px) {
    grid-template-columns: repeat(2, max-content);
    column-gap: 47px;
    row-gap: 64px;
  }

  @media (max-width: 992px) {
    grid-template-columns: max-content;
    justify-content: center;
  }
}

.gallery-inner__cards--col4 {
  grid-template-columns: repeat(4, max-content);
  column-gap: 59px;
  row-gap: 80px;
  margin-top: 40px;
  margin-bottom: 78px;
}

.gallery-inner__cards li {
  position: relative;
}

.gallery-inner__cards .card::after {
  top: -20px;
  right: -20px;
}

.gallery-inner__cards .msfavorites-text-add,
.gallery-inner__cards .msfavorites-text-remove {
  position: absolute;
  display: flex;
  width: 40px;
  height: 37px;
  right: 0;
  bottom: 205px;
  font-size: 0;
  background-repeat: no-repeat;
  background-size: 100% auto;
  cursor: pointer;

  @media (max-width: 1600px) {
    width: 32px;
    height: 30px;
    bottom: 195px;
  }

  @media (max-width: 1280px) {
    width: 27px;
    height: 25px;
    bottom: 190px;
  }

  @media (max-width: 1080px) {
    width: 32px;
    height: 30px;
    bottom: 196px;
  }
}

.gallery-inner__cards .msfavorites-text-add {
  background-image: url("../img/icon-heart.png");
}

.gallery-inner__cards .msfavorites-text-remove {
  display: none;
  background-image: url("../img/icon-heart-active.png");
}
