.nav-collection {
  max-width: 290px;

  @media (max-width: 1600px) {
    max-width: 232px;
  }

  @media (max-width: 1280px) {
    max-width: 185px;
  }

  @media (max-width: 1080px) {
    max-width: 278px;
  }

  @media (max-width: 992px) {
    max-width: 50%;
    text-align: center;
  }
}

.nav-collection__item {
  margin-bottom: 87px;

  @media (max-width: 1600px) {
    margin-bottom: 70px;
  }

  @media (max-width: 1280px) {
    margin-bottom: 57px;
  }

  @media (max-width: 1080px) {

  }
}

.nav-collection__title {
  position: relative;
  font-family: "Lady Marmalade", "Times", serif;
  font-size: 34px;
  text-transform: uppercase;
  color: var(--basic-blue);
  text-decoration: none;
  margin: 0;

  @media (max-width: 1600px) {
    font-size: 28px;
  }

  @media (max-width: 1280px) {
    font-size: 23px;
  }

  @media (max-width: 1080px) {
    font-size: 34px;
  }
}

.nav-collection__title::after {
  position: absolute;
  display: block;
  content: "";
  width: 113px;
  height: 12px;
  background-image: url("../img/vignette-small.png");
  background-repeat: no-repeat;
  background-size: 100% auto;

  @media (max-width: 1600px) {
    width: 94px;
    height: 9px;
  }

  @media (max-width: 1280px) {
    width: 75px;
    height: 7px;
  }

  @media (max-width: 1080px) {
    width: 113px;
    height: 12px;
  }

  @media (max-width: 992px) {
    left: 50%;
    transform: translateX(-50%);
  }
}

.nav-collection__item p {
  font-family: inherit;
  font-size: 34px;
  line-height: 36px;
  font-weight: 100;
  color: var(--basic-blue);

  @media (max-width: 1600px) {
    font-size: 28px;
    line-height: 30px;
  }

  @media (max-width: 1280px) {
    font-size: 23px;
    line-height: 25px;
  }

  @media (max-width: 1080px) {
    font-size: 34px;
    line-height: 36px;
  }
}

.nav-collection__item p > a {
  font-weight: normal;
  color: var(--basic-blue);
}
