.button {
  display: block;
  font-family: inherit;
  font-size: 42px;
  line-height: 50px;
  text-transform: uppercase;
  text-align: center;
  color: var(--basic-white);
  background-color: var(--basic-green);
  border-radius: 10px;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  text-decoration: none;

  @media (max-width: 1600px) {
    font-size: 35px;
    line-height: 40px;
  }

  @media (max-width: 1280px) {
    font-size: 28px;
    line-height: 31px;
    padding-top: 8px;
    padding-bottom: 8px;
  }

  @media (max-width: 1080px) {
    font-size: 42px;
    line-height: 50px;
    padding-top: 10px;
    padding-bottom: 10px;
  }
}
